import { makeStyles } from '@material-ui/core';

export default makeStyles(() => ({
    appBar: {
        borderRadius: 15,
        margin: '30px 0',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
    },
    heading: {
        color: 'rgba(0,118,55,99)',
    },
    image: {
        marginLeft: '15px',
    },
    mainContainer: {
        flexDirection: "column-reverse",
    },
    note: {
        borderRadius: 15,
    },
    normlink: {
        underline: 'none',
    },
    button: {
        marginTop: '5px',
    },
    text: {
        padding: '20px'
    },
    link: {
        alignItems: 'center',
    },
    linkimage: {
        alignSelf: 'center',
        width: '20%',
        maxwidth: '100px',
    }
}));